<template>
  <div class="score-rate">
    <ProgressBar
      class="car-card-section__progress-bar"
      :percent="localPercent"
      :lineWidth="12"
      :barColor="progressColor"
      :scaleLength="0"
    >
      <div class="score-rate__face" :class="rateClass"></div>
    </ProgressBar>
    <div class="score-rate__text">
      <UiTextString size="xs" weight="500" color="grey" align="center">
        все <br />
        хорошо
      </UiTextString>
      <UiTextString size="xs" weight="500" color="grey" align="center">
        цены <br />
        выше
      </UiTextString>
    </div>
    <div class="score-rate__rating">
      <UiTextString size="xs" weight="500" align="center">
        {{ rating }}
      </UiTextString>
    </div>
  </div>
</template>

<script>
import UiTextString from '@/components/core/ui-text-string/ui-text-string.vue';
import ProgressBar from '@/components/progress-bar/progress-bar.vue';

const MAX_PERCENT = 80;

export default {
  components: {
    ProgressBar,
    UiTextString,
  },

  props: {
    percent: {
      type: Number,
      required: true,
    },
  },

  computed: {
    rateSvg() {
      return 'GoodSvg';
    },

    localPercent() {
      return (MAX_PERCENT * this.percent) / 100;
    },

    progressColor() {
      if (this.isNeutralRating || this.isGoodRating || this.isExcellentRating) {
        return '#B2D803';
      }

      return '3E4958';
    },

    rateClass() {
      return {
        'score-rate__face_2': this.isBadRating,
        'score-rate__face_3': this.isNeutralRating,
        'score-rate__face_4': this.isGoodRating,
        'score-rate__face_5': this.isExcellentRating,
      };
    },

    isBadRating() {
      return this.localPercent <= 25;
    },

    isNeutralRating() {
      return this.localPercent >= 25 && this.localPercent <= 50;
    },

    isGoodRating() {
      return this.localPercent >= 50 && this.localPercent <= 75;
    },

    isExcellentRating() {
      return this.localPercent >= 75 && this.localPercent <= 100;
    },

    rating() {
      if (this.isBadRating) return 'Все плохо';
      if (this.isNeutralRating) return 'Нормально';
      if (this.isGoodRating) return 'Все хорошо';
      if (this.isExcellentRating) return 'Все отлично';

      return 'Вы заблокированы!';
    },
  },
};
</script>

