import Vue from 'vue';
import Vuex from 'vuex';
import UserService from '@/services/user-page';

Vue.use(Vuex);

export const USER_PAGE_STORE = 'USER_PAGE_STORE';

export default {
  namespaced: true,
  state: {
    user: {},
    totalRides: 0,
    totalExpenses: 0,
    inaccurateRides: [],
    documents: [],
    rideHistory: []
  },
  actions: {
    async LOAD_DATA({ commit }) {
      const { data } = await UserService.getData();

      commit('SET_DATA', data);
    },
  },

  mutations: {
    SET_DATA(state, payload) {
      state.user = payload.user;
      state.totalRides = payload.totalRides;
      state.totalExpenses = payload.totalExpenses;
      state.inaccurateRides = payload.inaccurateRides;
      state.documents = payload.documents;
      state.rideHistory = payload.rideHistory;
    }
  },

};
